*{margin: 0;padding: 0;font-family: 'Noto Sans KR', sans-serif;}
li{list-style: none;}
a{text-decoration: none;color: black;cursor: pointer;}



/* ********************* header ***************************** */


.header{
    display: flex;
    width: 100%;
    height: 100px;
    line-height: 100px; 
    justify-content: space-between;   
}


.header .logo a{
    font-size: 0px;
    display: block;
    width: 140px;
    background-image: url(../img/logo_navy.png);
    background-repeat: no-repeat;
    background-position: center center;
    background-size: 58%;
}

.header .gnb ul{
    display: flex;
}

.header .gnb ul li a{
    padding: 0 20px;
    font-size: 15px;
    font-weight: 700;
}

.header .gnb ul li:last-child a{
    border-left: 1px solid black;
}


.header .icon ul{
    display: flex;
}

.header .icon{margin-right: 20px;}
.header .icon ul li{margin: 0 20px;}


/* ********************* page_Stage ***************************** */

.page_stage{
    width: 500px;
    margin: 70px auto 20px;
    position: relative;
}

.page_stage ul{
    display: flex;
    justify-content: space-between;
    text-align: center;
}


.page_stage ul::before{
    content: "";
    position: absolute;
    z-index: -1;
    width: calc(100% - 40px); 
    height: 1px;
    top: 30%;left: 12px;
    background-color: black;
    margin-left: 10px;
}

.page_stage ul .num{
    margin: 0 auto;
    text-align: center;
    height: 40px;width: 40px;
    border-radius: 50px;
    line-height: 40px;
    font-weight: bold;
    box-shadow: 1px 10px 30px rgba(128, 128, 128, 0.593);
    background-color: #ccc;
    border: none;
}




/* ********************* wrap ***************************** */

.wrap{
    width: 800px;
    margin: 0 auto 70px;
    padding: 20px;
}


.wrap .visual_main img{
    width: 800px;
    margin: 50px 0;
}


.wrap p{
    text-align: center;
    margin-bottom: 10px;
    font-size: 15px;
    color: rgb(54, 54, 54);
}

.wrap .content{
    font-size: 13px;
    padding-bottom: 40px;
    border-bottom: 1px solid #000;
}


.wrap p span{
    font-weight: bold;
}






/* ********************* wrap_btn ***************************** */


.wrap .btn li:nth-child(1) a button{
    float: left;margin-right: 2%;
    background-color: #fff;
}

.wrap .btn li:nth-child(2) a button{
    float: left;
    background-color: rgb(0,32,83);
    color: white;
}

.wrap .btn li a button{
    width: 49%;
    height: 50px;
    border: 1px solid rgb(0,32,83);
    margin-bottom: 10px;
    font-size: 15px;
    letter-spacing: 2px;
    border-radius: 30px;
    font-weight: bold;
    cursor: pointer;
    margin-top: 20px;
}